import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { ContactsService } from '../contacts.service';
import { Router } from '@angular/router';
import { Contact } from '../model/model.contact';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css'],
  providers: [ ContactsService ]
})
export class ContactsComponent implements OnInit {
  contacts: any;
  motCle: string = "";
  currPage: number = 0;
  size: number = 5;
  pages: Array<number>;
  constructor(private http: Http, private contactService: ContactsService, private router: Router) { }

  ngOnInit() {
    this.getBySearch();
  }

  getBySearch(){
    this.contactService.searchContacts(this.motCle, this.currPage, this.size)
                       .subscribe(
                          data => {
                            this.contacts = data;
                            this.pages = new Array(data.totalPages)                    
                        },
                          err => {
                            console.log(err);
                        }
                        );
  }

  /*chercher(){
    this.getBySearch();
  }*/

  searchChanged(){
    this.getBySearch();
    console.log("search onKeyPress");
  }

  gotoPage(i: number){
    this.currPage = i;
    this.getBySearch();
  }

  onEditContact(id: number){
    this.router.navigate(['/edit-contact', id]);
  }

  onDeleteContact(c: Contact){
    if(confirm("Supprimer ? ")) {
      this.contactService.deleteById(c.id)
                        .subscribe(
                          data => {
                            alert("supprimé avec succès !");
                            this.contacts.content.splice(
                              this.contacts.content.indexOf(c), 1
                            );
                            //this.router.navigate(['/contacts']);
                            this.getBySearch();
                        },
                          err => {
                            console.log(err);
                        }
                        );
      console.log('oui');
    }
  }

}



